<template>
  <div class="demo-page svg-demo">
    <el-row>

      <el-col :span="24">
        <div class="svg__wrapper">
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="show-border">
            <g fill="none">
              <path d="M 20 20 l 250 0" v-bind="strokeConfig.stroke1"/>
              <path d="M 20 120 l 250 0" v-bind="strokeConfig.stroke2"/>
              <path d="M 20 220 l 250 0" v-bind="strokeConfig.stroke3"/>
            </g>
          </svg>
        </div>
        <div class="svg-config-form__wrapper">
          <el-form ref="circleConfigForm" :model="strokeConfig" label-width="200px">
            <el-form-item label="笔划颜色(stroke1)">
              <el-color-picker v-model="strokeConfig.stroke1.stroke" show-alpha >
              </el-color-picker>
              {{strokeConfig.stroke1.stroke}}
            </el-form-item>
            <el-form-item label="笔划颜色(stroke2)">
              <el-color-picker v-model="strokeConfig.stroke2.stroke" show-alpha >
              </el-color-picker>
              {{strokeConfig.stroke2.stroke}}
            </el-form-item>
            <el-form-item label="笔划颜色(stroke3)">
              <el-color-picker v-model="strokeConfig.stroke3.stroke" show-alpha >
              </el-color-picker>
              {{strokeConfig.stroke3.stroke}}
            </el-form-item>

            <el-form-item label="笔划宽度(strokeWidth1)">
              <el-slider
                  v-model="strokeConfig.stroke1['stroke-width']"
                  :max="20"
                  :step="1"
                  show-stops>
              </el-slider>
            </el-form-item>
            <el-form-item label="笔划宽度(strokeWidth2)">
              <el-slider
                  v-model="strokeConfig.stroke2['stroke-width']"
                  :max="20"
                  :step="1"
                  show-stops>
              </el-slider>
            </el-form-item>
            <el-form-item label="笔划宽度(strokeWidth3)">
              <el-slider
                  v-model="strokeConfig.stroke3['stroke-width']"
                  :max="20"
                  :step="1"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="线头形状(stroke-linecap3)">
              <el-select  v-model="strokeConfig.stroke3['stroke-linecap']" clearable placeholder="请选择">
                <el-option label="butt" value="butt"/>
                <el-option label="round" value="round"/>
                <el-option label="square" value="square"/>
                <el-option label="default" value=""/>
              </el-select>
            </el-form-item>

            <el-form-item label="线条类型(stroke-dasharray3)">
              <el-select  v-model="strokeConfig.stroke3['stroke-dasharray']" clearable placeholder="请选择">
                <el-option label="5,5" value="5,5"/>
                <el-option label="10,10" value="10,10"/>
                <el-option label="20,10,5,5,5,10" value="20,10,5,5,5,10"/>
                <el-option label="default" value=""/>
              </el-select>
            </el-form-item>

          </el-form>
        </div>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import DemoPageMixin from '@/mixins/demo-page-mixin'
export default {
  name: 'SVGDemo001',
  mixins: [DemoPageMixin],
  data () {
    return {
      strokeConfig: {
        stroke1: {
          stroke: 'rgba(217, 26, 26, 0.8)',
          'stroke-width': 2,
          'stroke-linecap': 'butt',
          'stroke-dasharray': '5,5'
        },
        stroke2: {
          stroke: 'rgba(0, 0, 0, 0.8)',
          'stroke-width': 4,
          'stroke-linecap': 'round',
          'stroke-dasharray': '10,10'
        },
        stroke3: {
          stroke: 'rgba(28, 28, 210, 0.8)',
          'stroke-width': 6,
          'stroke-linecap': 'square',
          'stroke-dasharray': '20,10,5,5,5,10'
        }
      }
    }
  }
}
</script>

<style scoped>

</style>